body {
    margin: 0;
    padding: 0;
    border: none;
    background-color: rgb(253, 224, 197);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.title {
    width: fit-content;
    color: rgb(68, 68, 68);
    font-size: 5.5vh;
    width: 100%;
    text-align: center;
}

.background-container {
    background-color: rgba(240, 248, 255, 0.342);
    width: 100%;
    height: 15vh;
}


.page {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 60vw;
    height: fit-content;
    margin-left: auto;
    margin-right: auto;
    background-color: rgb(250, 234, 214, 0.5);
    box-shadow: 1px 1px 2px #dfd2b1;
}

